body{   
    box-sizing: border-box;
    background: rgb(238,174,202);
    background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
}

.wrapper{
    display: flex;
    height: auto;
    width: 100vw;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 20px;
}

.wrapper button{
    margin-top: 20px;
    padding: 10px 20px;
    background-color: cadetblu;
    color: black;
    font-weight: bold;
    font-size: 20px;
    cursor: pointer;
}

.accordian{
    width: 600px;
    
}

.item{
    background-color: cadetblue;
    border-style: inset;
    margin: 15px;
    padding: 8px 20px;
    align-items: center;
    justify-content: center;
    text-align: left;

}

.title{
    color: rgb(242, 242, 244);
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
}

.content{
    color: black;
    height: auto;
    align-items: center;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-weight: 700;
    font-size: medium;
    font-style: oblique;
    
}